import { FrontContactUsComponent } from './../app/front-end/front-contact-us/front.contact.component';
import { Routes, RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
import { HomeComponent } from '../app/front-end/home/home.component';
import { PageNotFoundComponent } from 'src/app/page-not-found/page-not-found.component';
import { UserAuthCanActive } from "../services/authentcation/user.auth.can.active.service";
import { AccountSettingComponent } from "src/app/back-end/account-setting/account-setting.component";
import { AdminAuthCanActive } from 'src/services/authentcation/admin.auth.can.active.service';
import { AboutUsComponent } from 'src/app/front-end/about-us/about.us.component';
import { SighInComponent } from 'src/app/back-end/sigh-in/sigh.in.component';
import { BackEndContactUsComponent } from 'src/app/back-end/back-end-contact-us/back.end.contact.us.component';
import { FrontOurServicesComponent } from 'src/app/front-end/our-services/front.our.services.component';

const FrontEndROUTS: Routes = [
    {
        path: '', component: HomeComponent,
        data: {
            isBackEnd: false
        }
    },
    {
        path: 'services', component: FrontOurServicesComponent,
        data: {
            isBackEnd: false
        }
    },
    {
        path: 'about-us', component: AboutUsComponent,
        data: {
            isBackEnd: false,
            isAboutUs:true
        }
    },
    {
        path: 'contact-us', component: FrontContactUsComponent,
        data: {
            isBackEnd: false,
            isContactUs: true
        }
    },


];
const BackEndROUTS: Routes = [
    {
        path: 'sign-in', component: SighInComponent,
        data: {
            isBackEnd: true
        }
    },
    {
        path: 'accountsetting', component: AccountSettingComponent, canActivate: [UserAuthCanActive],
        data: {
            breadcrumbs: ["accountSetting"],
            isBackEnd: true
        }
    },
    {
        path: 'setting',
        loadChildren: () => import('./back-end/settings.module').then(mod => mod.SettingsModule),
        canActivate: [UserAuthCanActive, AdminAuthCanActive],
        data: {
            isBackEnd: true
        }
    },
    {
        path: 'users',
        loadChildren: () => import('./back-end/users.module').then(mod => mod.UsersModule),
        canActivate: [UserAuthCanActive, AdminAuthCanActive],
        data: {
            isBackEnd: true
        }
    },
    {
        path: 'freights',
        loadChildren: () => import('./back-end/freights.module').then(mod => mod.FreightsModule),
        canActivate: [UserAuthCanActive, AdminAuthCanActive],
        data: {
            isBackEnd: true
        }
    },
    {
        path: 'financialTransfers',
        loadChildren: () => import('./back-end/financial.transfares.module').then(mod => mod.FinancialTransfersModule),
        canActivate: [UserAuthCanActive, AdminAuthCanActive],
        data: {
            isBackEnd: true
        }
    },

    {
        path: 'reports',
        loadChildren: () => import('./back-end/reports.module').then(mod => mod.ReportsModule),
        canActivate: [UserAuthCanActive],
        data: {
            isBackEnd: true
        }
    },
    {
        path: 'sectorSales',
        loadChildren: () => import('./back-end/sector.sales.module').then(mod => mod.SectorSalesModule),
        canActivate: [UserAuthCanActive],
        data: {
            isBackEnd: true
        }
    },
    {
        path: 'deletes',
        loadChildren: () => import('./back-end/deletes.module').then(mod => mod.DeletesModule),
        canActivate: [UserAuthCanActive],
        data: {
            isBackEnd: true
        }
    },
    {
        path: 'containers',
        loadChildren: () => import('./back-end/containers.module').then(mod => mod.ContainersModule),
        canActivate: [AdminAuthCanActive],
        data: {
            isBackEnd: true
        }
    },
    {
        path: 'branches',
        loadChildren: () => import('./back-end/branches.module').then(mod => mod.BranchesModule),
        canActivate: [AdminAuthCanActive],
        data: {
            isBackEnd: true
        }
    },

    {
        path: 'contactUsMessages',
        loadChildren: () => import('./back-end/contact.us.messages.module').then(mod => mod.ContactUsMessagesModule),
        canActivate: [AdminAuthCanActive],
        data: {
            isBackEnd: true
        }
    },
];



const ROUTS: Routes = [
    ...FrontEndROUTS,
    ...BackEndROUTS,
    {
        path: "**",
        component: PageNotFoundComponent,
        data:{
            is404:true
        }
    }
]
@NgModule({
    imports: [RouterModule.forRoot(ROUTS, { relativeLinkResolution: 'legacy' })],
    exports: [RouterModule]
})
export class AppRoutingModule {

}

