import { Injectable } from '@angular/core';
import { CanActivate, Router } from "@angular/router";
import { RolesEnum } from 'src/enums/roles.enum';
import { CurrentUserService } from "./current.user.service";
/**
 * هنا ما يهمنا فقط ان يكون المستخدم ادمن
 */
@Injectable()
export class AdminAuthCanActive implements CanActivate {
    constructor(private u: CurrentUserService,private router:Router) {
    }

    canActivate() {
        if (this.u.u.roleId!=RolesEnum.Admin) {
            this.router.navigate(['/404'])
        }
        return this.u.isUserlogged;
    }

}//End Class