import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AlertService } from 'src/services/alert.service';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';
import { HttpService } from 'src/services/http/http.service';
import { SEOService } from "src/services/seo.service";
@Component({
  selector: 'app-front-contact',
  templateUrl: './front.contact.component.html',
  styleUrls: [
  ]
})
export class FrontContactUsComponent implements OnInit {
  formSubmitError;
  isRequestingToServer: boolean = false;

  form = new FormGroup({
    name: new FormControl(null, [Validators.required, Validators.maxLength(50)]),
    email: new FormControl(null, [Validators.required, Validators.email]),
    subject: new FormControl(null, [Validators.required, Validators.maxLength(100)]),
    message: new FormControl(null, [Validators.required, Validators.maxLength(4000)])
  });
  constructor(public u: CurrentUserService, private seo: SEOService, private http: HttpService) { }

  ngOnInit(): void {
    this.seo.updateSEO("contactUs");
  }

  /**
   * Send Message
   */
  sendMessage() {
    if (this.form.invalid) {
      this.formSubmitError = true;
      return;
    }
    this.formSubmitError = false;
    this.isRequestingToServer = true;
    this.form.disable();
    this.http.sednContactUsMessage(this.form.value).subscribe(res => {
      AlertService.showResponseMessage(res);
      if (res.isSuccess)
      {
        this.form.enable();
        this.form.reset();
        this.form.updateValueAndValidity();
      }
    }, error => {
      AlertService.canRequestError(error);
    }, () => {
      this.formSubmitError = true;
      this.isRequestingToServer = false;
      this.form.enable();
    })
  }



}// End Class
